import Vue from 'vue'
import App from './App.vue'
import Mint from 'mint-ui' //mint-ui 
import 'mint-ui/lib/style.css' // mint-ui css
import VueRouter from 'vue-router' //
import router from './router.js' //
import VueResource from 'vue-resource' //resource

Vue.use(Mint)
Vue.use(VueRouter)
Vue.use(VueResource)
// 
const app = new Vue({
	router:router,
	render:h=>h(App)
}).$mount('#app')



