import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from './component/index/index.vue'
import Home from './component/HomePage/home.vue' //
import Search from './component/search/search.vue' //
import Message from './component/message/message.vue' //
import Mess from './component/message/mess.vue' // 
import Dyna from './component/message/dyna.vue' // 
import Me from './component/me/me.vue' //
import myLike from './component/myLike/myLike.vue'
import Personal from './component/personal/personal.vue' //
import History from './component/history/history.vue' //
import Collect from './component/collect/collect.vue' //
import Est from './component/collect/est.vue' //
import Att from './component/collect/att.vue' //
import Label from './component/label/label.vue' //
import Yet from './component/label/yet.vue' //
import All from './component/label/all.vue' //
import Setting from './component/set/set.vue' //
Vue.use(VueRouter)
// 
const router = new VueRouter({
	routes:[
	{
		path:'/',
		redirect:'/home' //
	},
	{
		path:'/index',
		component:Index, //
		children:[
			{
		path:'/home',
		component:Home //
		},
		{
			path:'/search',
			component:Search //
		},
		{
			path:'/message',
			component:Message, //
			children:[
			{
				path:"/",
				redirect:"/mess"
			},
			{
				path:"/mess",
				component:Mess //
			},
			{
				path:"/dyna",
				component:Dyna //
			}
			]
		},
		{
			path:'/me',
			component:Me //
		}
			]
		},
	{
		path:"/personal",
		component:Personal //
	},
	{
		path:"/myLike",
		component:myLike //
	},
	{
		path:"/collect",
		component:Collect, //
		children:[
		{
			path:"/",
			redirect:"/est"
		},
		{
			path:"/est",
			component:Est //
		},
		{
			path:"/att",
			component:Att //
		}
		]
	},
	{
		path:"/history",
		component:History //
	},
	{
		path:"/label",
		component:Label, //
		children:[
		{
			path:"/",
			redirect:"/yet"
		},
		{
			path:"/yet",
			component:Yet //
		},
		{
			path:"/all",
			component:All //
		}
		]
	},
	{
		path:"/set",
		component:Setting //
	}
	]
})
export default router;


